/* ==========================================================================
    Scene transitions
   ========================================================================== */
.mp-scene .mp-element,
.mp-scene .mp-elem {
  animation-duration: .5s;
  animation-timing-function: ease-out;
  animation-fill-mode: both;
}

.mp-scene .mp-scale {
  animation-name: scale;
  will-change: transform, opacity;
}

.mp-scene .mp-iris {
  animation-name: iris;
  will-change: clip-path;
}

.mp-scene .mp-iris.is-exiting {
  animation-delay: .5s;
}

.mp-scene .mp-fade,
.mp-scene .mp-enter-fade {
  animation-name: fade;
  will-change: opacity;
}

.mp-scene .mp-one.mp-scale {
  animation-name: slideLeft;
  animation-timing-function: cubic-bezier(0.68, -0.25, 0.265, 1.25);
}

.mp-scene .mp-two.mp-scale {
  animation-name: slideRight;
  animation-timing-function: cubic-bezier(0.68, -0.25, 0.265, 1.25);
}

.mp-scene .mp-slideUp,
.mp-scene.is-exiting .mp-exit-slideUp {
  animation-name: slideUp;
  animation-timing-function: cubic-bezier(0.68, -0.25, 0.265, 1.25);
}

.mp-scene .mp-slideInRight {
  animation-name: slideInRight;
  animation-timing-function: cubic-bezier(0.68, -0.25, 0.265, 1.25);
}

.mp-scene .mp-enter-fade {
  animation-name: fade;
  will-change: opacity;
}

.mp-scene .mp-slideDown {
  animation-name: slideDown;
  will-change: transform, opacity;
}

.mp-scene .mp-two .mp-element {
  animation-delay: .4s;
}

.mp-scene.is-exiting .mp-element,
.mp-scene.is-exiting .mp-exit-slideUp {
  animation-direction: alternate-reverse;
}

.mp-scene .mp-two .mp-delay-1 {
  animation-timing-function: cubic-bezier(0.68, -0.25, 0.265, 1.25);
  animation-delay: .1s;
}

.mp-scene .mp-two .mp-delay-2 {
  animation-timing-function: cubic-bezier(0.68, -0.25, 0.265, 1.25);
  animation-delay: .3s;
}

.mp-scene .mp-two .mp-delay-3 {
  animation-timing-function: cubic-bezier(0.68, -0.25, 0.265, 1.25);
  animation-delay: .5s;
}

@keyframes scale {
  0% {
    transform: translate3d(100%, 0, 0);
  }
  100% {
    transform: none;
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes slideUp {
  0% {
    transform: translate3d(0, -100%, 0);
  }
  100% {
    transform: none;
  }
}

@keyframes slideDown {
  0% {
    transform: translate3d(0, 10%, 0);
    opacity: 0;
  }
  100% {
    transform: none;
    opacity: 1;
  }
}

@keyframes slideLeft {
  0% {
    transform: translate3d(-100%, 0, 0);
  }
  100% {
    transform: none;
  }
}

@keyframes slideRight {
  0% {
    transform: translate3d(100%, 0, 0);
  }
  100% {
    transform: none;
  }
}

@keyframes slideInRight {
  0% {
    transform: translate3d(130%, 0, 0);
    opacity: 0;
  }
  100% {
    transform: none;
    opacity: 1;
  }
}

@keyframes iris {
  0% {
    clip-path: circle(0% at 50% 50%);
  }
  100% {
    clip-path: circle(100% at 50% 50%);
  }
}

/* ==========================================================================
    Navigation
   ========================================================================== */
.mp-nav {
  position: relative;
  padding-right: 2em;
  user-select: none;
  text-align: right;
}

.mp-logo {
  position: absolute;
  z-index: 200;
  top: .9em;
  left: 1em;
  width: 1.25em;
  height: 1.25em;
  transition: all .3s ease-out;
}

.mp-home .mp-logo {
  top: 1.3em;
  left: 2em;
  width: 60px;
  height: 60px;
  padding: 1em;
  background-color: #eb5757;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.5);
}

.mp-logo-svg {
  fill: rgba(255, 255, 255, 0.6);
}

.mp-crumb {
  font-weight: 700;
  line-height: 44px;
  position: absolute;
  top: -11px;
  left: 2.5em;
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.6);
}

.mp-home .mp-logo-svg {
  fill: #fff;
}

.mp-mobile {
  line-height: 50px;
  color: #e0e0e0;
}

.mp-nav-list {
  display: block;
  height: 0;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.mp-nav-item {
  font-size: 0;
  display: inline-block;
}

.mp-nav-link {
  font-size: 14px;
  line-height: 44px;
  position: relative;
  display: inline-block;
  padding-right: 1em;
  padding-left: 1em;
  cursor: default;
  text-decoration: none;
  text-transform: uppercase;
  color: #e0e0e0;
}

.mp-nav-link:hover {
  font-weight: bold;
}

.mp-nav-link:after {
  font-weight: bold;
  display: block;
  visibility: hidden;
  overflow: hidden;
  height: 1px;
  margin-bottom: -1px;
  content: attr(title);
  text-transform: uppercase;
  color: transparent;
}

.current {
  color: #fff;
}

.mp-mobile {
  line-height: 48px;
  display: block;
  cursor: default;
  background-color: #1e282d;
  background-image: linear-gradient(to bottom, #32434c, #28363c);
}

@media (max-width: 705px) {
  .mp-nav {
    position: fixed;
    z-index: 1;
    z-index: 100;
    top: 0;
    width: 100%;
    background-color: #1e282d;
    background-image: linear-gradient(to bottom, #32434c, #28363c);
  }
  .mp-nav-list {
    position: absolute;
    z-index: 30;
    top: 48px;
    left: 0;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 0;
    padding: 0;
    transition: all .2s .2s ease-out;
    background: linear-gradient(-45deg, #ec65b7 0%, #05e0d8 100%);
    justify-content: stretch;
    -webkit-clip-path: circle(0% at 85% 0%);
    clip-path: circle(0% at 85% 0%);
  }
  .mp-nav-link {
    display: flex;
    overflow: hidden;
    height: 0;
    padding: 0;
    transition: height 0.2s cubic-bezier(0.465, 0.2, 0.03, 1);
    text-align: center;
    justify-content: center;
    align-items: center;
  }
  .mp-made {
    overflow-y: hidden;
    height: 0;
  }
  .is-open .mp-nav-item + .mp-nav-item .mp-nav-link {
    border-top: 1px solid rgba(255, 255, 255, 0.6);
  }
  .mp-nav-link:after {
    content: '';
  }
  .is-open .mp-nav-list {
    height: calc(100vh - 48px);
    transition: all .2s ease-out;
    -webkit-clip-path: circle(150% at 85% 0%);
    clip-path: circle(150% at 85% 0%);
  }
  .is-open .mp-nav-link {
    width: 80%;
    height: calc(18vh - 15px);
    margin: auto;
  }
  .mp-nav .mp-made {
    transition: transform 0.3s cubic-bezier(0.465, 0.2, 0.03, 1);
    transform: translate(-50%, 50px);
  }
  .is-open .mp-made {
    position: fixed;
    z-index: 200;
    bottom: 12px;
    left: 50%;
    height: 25px;
    transform: translate(-50%, 0);
    white-space: nowrap;
  }
}

@media (min-width: 705px) {
  .mp-nav {
    padding-bottom: 1em;
  }
  .mp-logo {
    top: 0;
    left: 2em;
    width: 80px;
    height: 80px;
    padding: 1.5em;
  }
  .mp-home .mp-logo {
    top: 2.5em;
    left: 8em;
    width: 80px;
    height: 80px;
    padding: .75em 1em;
    background-color: #eb5757;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.5);
  }
  .mp-logo-svg {
    fill: #fff;
  }
  .mp-crumb {
    display: none;
  }
  .mp-nav-list {
    display: inline-block;
    padding-top: 1em;
  }
  .mp-mobile {
    display: none;
  }
  .mp-nav-current {
    position: absolute;
    bottom: 1.25em;
    width: 0;
    height: 0;
    margin-left: -5px;
    content: '';
    transition: all .3s ease-out;
    border-width: 0 5px 8px;
    border-style: solid;
    border-color: transparent transparent #fff;
  }
  .mp-home .mp-nav-current {
    right: 371px;
  }
  .mp-blog .mp-nav-current {
    right: 301px;
  }
  .mp-lab .mp-nav-current {
    right: 237px;
  }
  .mp-portfolio .mp-nav-current,
  .mp-digital .mp-nav-current,
  .mp-interface .mp-nav-current,
  .mp-traditional .mp-nav-current {
    right: 155px;
  }
  .mp-about .mp-nav-current {
    right: 65px;
  }
  .mp-nav .mp-made {
    display: none;
  }
}

/* ==========================================================================
    Content
   ========================================================================== */
.mp-content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  background-color: #fff;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.35), 0 10px 10px rgba(0, 0, 0, 0.32);
}

.mp-wrap {
  display: flex;
}

.is-post.mp-content {
  position: relative;
  min-height: calc(100vh - 130px);
}

@media (max-width: 1000px) {
  .mp-content,
  .is-post.mp-content {
    position: relative;
    min-height: 100vh;
  }
}

@media (min-width: 1000px) {
  .mp-wrap {
    height: 100%;
  }
}

.mp-pane {
  flex: 0 0 50%;
}

/* ==========================================================================
    Author
   ========================================================================== */
.mp-author {
  display: flex;
  margin-top: 2em;
  padding-top: 1.5em;
