/* ==========================================================================
    Home Page
   ========================================================================== */

.mp-one,
.mp-two {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.mp-home .mp-one {
    position: relative;
    overflow: hidden;

    color: #fff;
    background-color: #83827e;
    background-size: cover;
}

.mp-img-loader:before,
.mp-img-loader:after {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: '';
}

.mp-img-loader:after {
    opacity: 0.5;
}

.mp-img-loader:before {
    overflow: hidden;

    transform: scale(1.2);
    -webkit-filter: blur(6px);
    filter: blur(6px);
}

@supports (mix-blend-mode: soft-light) {
    .mp-post-image:not(.is-expanded):after {
        background: linear-gradient(-45deg, rgba(236, 101, 183, 0.5) 0%, rgba(5, 224, 216, 0.5) 100%);
        mix-blend-mode: hard-light;

        .mp-traditional & {
            background: linear-gradient(-45deg, rgba(233, 30, 99, 0.5) 0%, rgba(156, 39, 176, 0.5) 100%);
        }

        .mp-interface & {
            background: linear-gradient(-45deg, rgba(76, 175, 80, 0.5) 0%, rgba(205, 220, 57, 0.5) 100%);
        }

        .mp-digital & {
            background: linear-gradient(-45deg, rgba(255, 235, 59, 0.5) 0%, rgba(255, 87, 34, 0.5) 100%);
        }
    }

    .mp-post-content .mp-img-loader:after {
        background: none;
    }
}

.mp-home .mp-one:before {
    background-image: url(data:image/jpg;base64,);
}

.mp-one .mp-pane-motto,
.mp-two .mp-pane-motto {
    font-family: 'Futura', sans-serif;
    font-weight: 900;
    line-height: 1;
    text-transform: uppercase;

    @include responsiveNum(font-size, 48px, 96px);
}

.mp-one .mp-pane-motto {
    display: none;
}

.mp-one .mp-pane-motto:first-child {
    margin-top: 1em;
    border-top: 2px solid #fff;
}

.mp-one .mp-pane-motto:last-child {
    margin-bottom: 1em;
    border-bottom: 2px solid #fff;
}

.mp-two .mp-pane-motto:first-child {
    border-top: 3px solid lighten($text-color, 20%);
}

.mp-two .mp-pane-motto:nth-child(3) {
    border-bottom: 3px solid lighten($text-color, 20%);
}

.mp-home .mp-two {
    padding-top: 2em;
}

.mp-article-text,
.mp-home-text {
    position: relative;
    z-index: 12;
    width: 60%;
    max-width: 440px;

    text-align: center;

    @include responsiveNum('margin-top', 2em, 4em);
}

.mp-blog .mp-one .mp-motto {
    padding-top: 45px;
}

.mp-socials {
    display: flex;
    width: calc(100% - 12em);
    justify-content: space-between;
    align-items: flex-start;
    flex: 0 1 auto;

    .mp-about & {
        width: 100%;
    }

    a:after:hover {
        width: 0;
    }

    @include responsiveNum('margin-top', 14px, 32px);
}

@media (max-width: #{$break}) {
    .mp-wrap {
        position: relative;
        flex-direction: column;
        min-height: calc(100vh - 86px);
    }

    .mp-one {
        flex: 1 0 65%;
    }

    .mp-two {
        flex: 1 0 35%;
    }

    .mp-one .mp-pane-motto {
        display: block;
    }

    .mp-two .mp-pane-motto {
        display: none;
    }

    .mp-home-text {
        margin-top: 0;
    }
}

@media (min-width: #{$mobile}) {
    .mp-one {
        background-position: 50% 70%;
    }
}

@media (max-width: #{$mobile}) {
    .mp-wrap {
        top: 48px;
        min-height: calc(100vh - 48px);
    }

    .mp-socials {
        flex: 1 1 100px;
    }
}
