/* ==========================================================================
    Portfolio
   ========================================================================== */

.mp-portfolio .mp-one,
.mp-digital .mp-one,
.mp-interface .mp-one,
.mp-traditional .mp-one {
    background-image: url(data:image/jpg;base64,);

    & .mp-pane-motto {
        display: block;

        color: #fff;
        border-top: 2px solid #fff;
        border-bottom: 2px solid #fff;
    }
}

.mp-pane-text {
    width: 60%;
    max-width: 440px;

    text-align: center;
    color: #fff;
    //margin-top: 4em;
    @include responsiveNum('margin-top', 2em, 4em);
}

.mp-gallery {
    width: 100%;
    margin: 0 auto;
    padding: 0;
    list-style-type: none;
}

.mp-portfolio-item,
.mp-gallery-item {
    position: relative;
    max-width: 75%;
    margin: auto;
    padding-top: 2em;
    padding-bottom: 2em;
}

.mp-portfolio-link,
.mp-gallery-link {
    position: relative;
    z-index: 6;
    display: inline-block;

    color: $text-color;
    text-decoration: none;

    &:hover {
        opacity: 0.8;
    }

    p {
        padding-top: 2em;
    }
}

.mp-portfolio-image {
    position: relative;
    height: 120px;
    background-position: 50% 20%;
    background-size: 100%;

    transition: all 0.2s ease-out;
}

/* stylelint-disable */
.mp-article-img:before,
.mp-portfolio-image:before {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: '';
    opacity: 0.5;
    background-image: -webkit-gradient(linear, left top, left bottom, from(black), color-stop(19%, rgba(0, 0, 0, 0.738)), color-stop(34%, rgba(0, 0, 0, 0.541)), color-stop(47%, rgba(0, 0, 0, 0.382)), color-stop(56.5%, rgba(0, 0, 0, 0.278)), color-stop(65%, rgba(0, 0, 0, 0.194)), color-stop(73%, rgba(0, 0, 0, 0.126)), color-stop(80.2%, rgba(0, 0, 0, 0.075)), color-stop(86.1%, rgba(0, 0, 0, 0.042)), color-stop(91%, rgba(0, 0, 0, 0.021)), color-stop(95.2%, rgba(0, 0, 0, 0.008)), color-stop(98.2%, rgba(0, 0, 0, 0.002)), to(rgba(0, 0, 0, 0)));
    background-image: linear-gradient(to bottom, black 0%, rgba(0, 0, 0, 0.738) 19%, rgba(0, 0, 0, 0.541) 34%, rgba(0, 0, 0, 0.382) 47%, rgba(0, 0, 0, 0.278) 56.5%, rgba(0, 0, 0, 0.194) 65%, rgba(0, 0, 0, 0.126) 73%, rgba(0, 0, 0, 0.075) 80.2%, rgba(0, 0, 0, 0.042) 86.1%, rgba(0, 0, 0, 0.021) 91%, rgba(0, 0, 0, 0.008) 95.2%, rgba(0, 0, 0, 0.002) 98.2%, rgba(0, 0, 0, 0) 100%);
}
/* stylelint-enable */

.mp-portfolio-image:hover {
    background-size: 110%;
    box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12);
    filter: grayscale(0.8);
}

.mp-portfolio-title {
    padding: 1em 0;

    font-family: 'Futura', sans-serif;
    font-size: 1.5em;
}

.mp-back-link {
    position: relative;
    display: inline-block;

    font-size: 12px;
    text-align: center;
}

.mp-gallery-page .mp-post-content {
    padding-top: 4em;
}

.mp-digital .mp-content,
.mp-interface .mp-content,
.mp-traditional .mp-content {
    position: relative;
    min-height: calc(100vh - 130px);
}
$gap: 1em;
$cellHeight: 12em;
$fullHeight: 2 * ($cellHeight + $gap);

.mp-cell-wrap {
    position: relative;
    max-width: $break;
    margin: 4em auto;
    padding-right: 2em;
    padding-left: 2em;
}

.mp-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.mp-cell {
    display: flex;
    flex: 0 0 calc(50% - (#{$gap} / 2));
    flex-wrap: wrap;
    justify-content: space-between;

    &:nth-child(even) {
        margin-left: $gap;
    }
}

.mp-cell-content {
    position: relative;
    box-sizing: border-box;
    max-width: calc(50% - (#{$gap} / 2));
    height: $cellHeight;
    margin-bottom: $gap;
    background-color: $text-color;
    background-position: 50%;
    background-size: cover;
    flex: 0 1 50%;
}

.mp-cell-link {
    position: absolute;
    width: 100%;
    height: 100%;
}

.mp-article-img {
    position: relative;
    background-size: 100%;

    transition: all 0.2s ease-out;
}

.mp-article-img:hover {
    background-size: 110%;
    filter: grayscale(0.2);
}

.big .mp-cell-content,
.wide-top .mp-cell-content:first-child,
.wide-bottom .mp-cell-content:last-child {
    max-width: 100%;
    margin-right: 0;
    flex: 1 0 100%;
}

.tall-right,
.tall-left {
    flex-direction: column;
    max-height: $fullHeight;
    align-self: flex-start;

    .mp-cell-content {
        width: calc(50% - 0em);
    }
}

.tall-left .mp-cell-content:first-child {
    height: $fullHeight;
    margin-right: $gap;
}

.tall-right .mp-cell-content:last-child {
    height: $fullHeight;
    margin-left: $gap;
}

.big .mp-cell-content {
    $adjHeight: $fullHeight - $gap;
    height: $adjHeight;
}

.mp-gallery-header {
    display: flex;
    width: 100%;
    padding: 2em;
    align-items: center;
}

.mp-gallery-title {
    position: relative;
    top: -4px;

    font-family: 'Futura', sans-serif;
    font-size: 1.5em;
    line-height: 1;
    color: $text-color;
}

.mp-gallery-meta {
    margin-left: auto;

    font-family: 'Futura', sans-serif;
    font-size: 0.8em;
    line-height: 1.25;
    text-align: right;
    color: lighten($text-color, 40);
}

.mp-gallery-image {
    //width: calc(100% - 4em);
    height: 60vh;
    margin: auto;
}

.mp-gallery-page .mp-gallery-image {
    background-color: #1b2225;
    background-size: contain;
}

.mp-gallery-page .mp-share {
    margin-bottom: 3em;
}

.mp-gallery-page .mp-img-loader:after {
    background-image: none;
}

@media (max-width: #{$mobile}) {
    .mp-digital  .mp-post-header,
    .mp-interface  .mp-post-header,
    .mp-traditional  .mp-post-header {
        margin-top: 0;
    }

    .mp-cell {
        flex: 0 0 100%;

        &:nth-child(even) {
            margin-left: 0;
        }
    }

    .mp-gallery-image {
        width: 100%;
    }

    .mp-gallery-header {
        padding: 4em 1em 2em;
    }

    .mp-gallery-page .mp-gallery-image {
        background-size: contain;
    }
}

@media (max-width: #{$break}) {
    .mp-digital .mp-content,
    .mp-interface .mp-content,
    .mp-traditional .mp-content {
        position: relative;
        min-height: 100vh;
    }
}
